<template>
  <div style="height: 100%;">
    <h1 class="text-center">Add Info</h1>
    <section class="content">
      <div class="row">
        <div class="col-md-12">
          <div class="box box-info">
            <!-- Input Addons -->
            <div class="box-header with-border">
              <form @submit.prevent="PushData">
                
                <div class="input">
                  <p>Report Month & Year*</p>
                  <month-picker-input v-model="form.month" required :no-default="true"></month-picker-input>
                </div>
                <br/>
                <div class="input">
                  <p>Location | Region*</p>
                  <select class="form-control" required v-model="form.region">
                    <option value="" disabled selected>Select Location | Region</option>
                    <option value="Central Region">Central Region</option>
                    <option value="Coast Region">Coast Region</option>
                    <option value="Eastern Region">Eastern Region</option>
                    <option value="HQ Nairobi">HQ Nairobi</option>
                    <option value="Nyanza Region">Nyanza Region</option>
                    <option value="Rift Valley Region">Rift Valley Region</option>
                    <option value="Western Region">Western Region</option>
                  </select>
                </div>
                <br/>
                <div class="input">
                  <p>Contract Staff*</p>
                  <input class="form-control" placeholder="Contract Staff*" type="number" v-model="form.contract_staff" required>
                </div>
                <br/>
                <div class="input">
                  <p>Depatures*</p>
                  <input class="form-control" placeholder="Depatures*" type="number" v-model="form.depatures" required>
                </div>
                <br/>
                <div class="input">
                  <p>Number Of New Contract Hires (MTH)*</p>
                  <input class="form-control" placeholder="Number Of New Contract Hires (MTH)*" type="number" v-model="form.new_contracts" required>
                </div>
                <br/>
                <div class="input">
                  <p>Total Staff For the Month*</p>
                  <input class="form-control" placeholder="Total Staff For the Month*" type="number" v-model="computedTotalStaff" disabled required>
                </div>
                <br/>
                <div class="input">
                  <p>SJA Associates - LOCUM*</p>
                  <input class="form-control" placeholder="SJA Associates - LOCUM*"  v-model="computedSJAAssociates" disabled required>
                </div>
                <br/>
                <div class="input">
                  <p>LOCUM Depatures*</p>
                  <input class="form-control" placeholder="LOCUM Depatures*"  v-model="form.locum_depatures" required>
                </div>
                <br/>
                <div class="input">
                  <p>SJA Associates - Volunteers*</p>
                  <input class="form-control" placeholder="SJA Associates - Volunteers*" type="number" v-model="form.sja_depatures" required>
                </div>
                <br/>
                <div class="input">
                  <p>Voluteer Depatures*</p>
                  <input class="form-control" placeholder="Voluteer Depatures*" type="number" v-model="form.volunteer_depatures" required>
                </div>
                <br/>
                <button type="submit" v-bind:class="'btn btn-primary btn-lg ' + loading">Submit</button>
              </form>
              <br/>
              <!-- errors -->
              <div v-if=response class="text-red"><p class="vertical-5p lead">{{response}}</p></div>

            </div>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import { MonthPickerInput } from 'vue-month-picker'
import axios from 'axios'

export default {
  name: 'Add Data',
  components: {
    MonthPickerInput
  },
  data(router) {
    return {
      form: {
        region: '',
        depatures: null,
        volunteer_depatures: null,
        locum_depatures: null,
        sja_depatures: null,
        sja_associates: null,
        total_staff_month: null,
        new_contracts: null,
        contract_staff: null,
        month: null
      },
      loading: '',
      response: ''
    }
  },
  methods: {
    PushData() {
      this.toggleLoading()
      this.resetResponse()
      this.$store.commit('TOGGLE_LOADING')
      this.form = { ...this.form, total_staff_month: this.computedTotalStaff, sja_associates: this.computedSJAAssociates, month: this.form.month.monthIndex, year: this.form.month.year }

      axios.post('http://178.79.130.84:5500/api/data/add/hr', this.form)
        .then(response => {
          this.toggleLoading()

          var data = response.data
          this.$router.push(data.redirect ? data.redirect : '/')
        })
        .catch(error => {
          this.response = error.message
          this.toggleLoading()
        })
    },
    toggleLoading() {
      this.loading = this.loading === '' ? 'loading' : ''
    },
    resetResponse() {
      this.response = ''
    }
  },
  computed: {
    computedTotalStaff() {
      if (this.form.new_contracts !== null || this.form.contract_staff !== null || this.form.depatures !== null) {
        const totalStaff = parseInt(this.form.new_contracts) + (parseInt(this.form.contract_staff) - parseInt(this.form.depatures))
        console.log('computedTotalStaff:', this.form)
        return totalStaff
      } else { return null }
    },
    computedSJAAssociates() {
      if (this.form.depatures !== null && this.computedTotalStaff !== null && this.form.new_contracts !== null) {
        const sjaAssociates = (parseInt(this.form.depatures) + parseInt(this.computedTotalStaff)) - parseInt(this.form.new_contracts)
        console.log('computedSJAAssociates:', sjaAssociates)
        return sjaAssociates
      } else { return null }
    }
  },
  watch: {
    form: function(newValue) {
      this.form.total_staff_month = this.computedTotalStaff
    }
  }
}
</script>

<style scoped>

</style>



