<template>
  <!-- Main content -->
  <section class="content">
    <div class="row">

      <!-- /.col -->
      <!-- <div class="col-md-6 col-sm-12 col-xs-12">
        <info-box color-class="bg-yellow"
                  :icon-classes="['ion', 'ion-ios-people-outline']"
                  text="Data "
                  :number="patients_number"></info-box>
      </div> -->
      <!-- /.col -->
    </div>
    <!-- /.row -->

    <div class="col-xs-12">
      <div class="box">
        <div class="box-header with-border">
          <h3 class="box-title"></h3>
          <div class="box-body">
            <div class="col-sm-6 col-xs-12">
              <p class="text-center">
                <strong>New Contracts By Region</strong>
              </p>
              <canvas id="trafficBar"></canvas>
            </div>
            <hr class="visible-xs-block">
            <div class="col-sm-6 col-xs-12">
              <p class="text-center">
                <strong>Depatures By Region</strong>
              </p>
              <canvas id="ageBar"></canvas>
            </div>
            <hr class="visible-xs-block">
            <div class="col-sm-6 col-xs-12">
              <p class="text-center">
                <strong>Total Staff By Region</strong>
              </p>
              <canvas id="languagePie"></canvas>
            </div>
          </div>
        </div>
        <div class="text-center">

        </div>
      </div>
    </div>
    <!-- /.row -->

    <!-- Main row -->
    <div class="row">
    </div>
    <!-- /.row -->
  </section>
  <!-- /.content -->
</template>

<script>
import Chart from 'chart.js'
import Alert from '../widgets/Alert'
import InfoBox from '../widgets/InfoBox'
import ProcessInfoBox from '../widgets/ProcessInfoBox'
import {mapGetters} from 'vuex'
import axios from 'axios'

export default {
  name: 'Dashboard',
  components: {
    Alert,
    InfoBox,
    ProcessInfoBox
  },
  data() {
    return {
      patients_number: '0',
      generateRandomNumbers(numbers, max, min) {
        var a = []
        for (var i = 0; i < numbers; i++) {
          a.push(Math.floor(Math.random() * (max - min + 1)) + max)
        }
        return a
      }
    }
  },
  computed: {
    coPilotNumbers() {
      return this.generateRandomNumbers(12, 1000000, 10000)
    },
    personalNumbers() {
      return this.generateRandomNumbers(12, 1000000, 10000)
    },
    isMobile() {
      return (window.innerWidth <= 800 && window.innerHeight <= 600)
    },
    ...mapGetters({
      user: 'user'
    })
  },
  mounted() {
    this.$nextTick(() => {
      console.log(window.localStorage.getItem('user'))
      axios.get('http://178.79.130.84:5500/api/data/get/hr')
        .then(res => {
          this.patients_number = res.data.hr_data
          let dataHR = res.data.hr_data
          // eslint-disable-next-line camelcase
          let labels_reg = []
          // eslint-disable-next-line camelcase
          let count_reg = []

          const groupedData = {}

          dataHR.forEach(item => {
            const region = item.region
            if (!groupedData[region]) {
              groupedData[region] = []
            }
            groupedData[region].push(item)
          })

          const maxDatesByRegion = []

          for (const region in groupedData) {
            const itemsInRegion = groupedData[region]
            const maxDateItem = itemsInRegion.reduce((max, item) => {
              return new Date(item.createdAt) > new Date(max.createdAt) ? item : max
            })
            maxDatesByRegion.push(maxDateItem)
          }

          maxDatesByRegion.forEach(e => {
            labels_reg.push(`${e.region}`)
            count_reg.push(e.new_contracts)
          })
          var ctx = document.getElementById('trafficBar').getContext('2d')
          var config = {
            type: 'bar',
            data: {
              labels: labels_reg,
              datasets: [{
                label: 'New Contracts',
                backgroundColor: 'blue',
                data: count_reg
              }]
            },
            options: {
              responsive: true,
              maintainAspectRatio: !this.isMobile,
              legend: {
                position: 'bottom',
                display: true
              },
              tooltips: {
                mode: 'label',
                xPadding: 10,
                yPadding: 10,
                bodySpacing: 10
              }
            }
          }

          new Chart(ctx, config) // eslint-disable-line no-new

          // eslint-disable-next-line camelcase
          let labels_age = []
          // eslint-disable-next-line camelcase
          let count_age = []

          maxDatesByRegion.forEach(e => {
            labels_age.push(`${e.region}`)
            count_age.push(e.depatures)
          })
          // eslint-disable-next-line camelcase
          var ctx_age = document.getElementById('ageBar').getContext('2d')
          // eslint-disable-next-line camelcase
          var config_age = {
            type: 'bar',
            data: {
              labels: labels_age,
              datasets: [{
                label: 'Registrations',
                data: count_age,
                backgroundColor: 'rgb(75, 192, 192)'
              }]
            },
            options: {
              scales: {
                yAxes: [{
                  ticks: {
                    beginAtZero: true
                  }
                }]
              },
              responsive: true,
              maintainAspectRatio: !this.isMobile,
              legend: {
                position: 'bottom',
                display: true
              },
              tooltips: {
                mode: 'label',
                xPadding: 10,
                yPadding: 10,
                bodySpacing: 10
              }
            }
          }
          new Chart(ctx_age, config_age) // eslint-disable-line no-new

          let label = []
          let data = []
          // gender chart

          maxDatesByRegion.forEach(e => {
            label.push(e.region)
            data.push(e.total_staff_month)
          })
          var pieChartCanvas = document.getElementById('languagePie').getContext('2d')
          var pieConfig = {
            type: 'pie',
            data: {
              labels: label,
              datasets: [{
                data: data,
                backgroundColor: ['rgb(255, 99, 132)',
                  'rgb(255, 159, 64)',
                  'rgb(255, 205, 86)',
                  'rgb(75, 192, 192)',
                  'rgb(54, 162, 235)',
                  'rgb(153, 102, 255)',
                  'rgb(201, 203, 207)'],
                hoverBackgroundColor: ['rgb(255, 99, 132)',
                  'rgb(255, 159, 64)',
                  'rgb(255, 205, 86)',
                  'rgb(75, 192, 192)',
                  'rgb(54, 162, 235)',
                  'rgb(153, 102, 255)',
                  'rgb(201, 203, 207)']
              }]
            },
            options: {
              responsive: true,
              maintainAspectRatio: !this.isMobile,
              legend: {
                position: 'bottom',
                display: true
              }
            }
          }
          new Chart(pieChartCanvas, pieConfig) // eslint-disable-line no-new
        })
    })
  }
}
</script>
<style>
.info-box {
  cursor: pointer;
}

.info-box-content {
  text-align: center;
  vertical-align: middle;
  display: inherit;
}

.fullCanvas {
  width: 100%;
}
</style>



