<template>
  <section class="content">
    <template>
      <div class="row center-block">
      <h2>Data pushed</h2>
      <div class="col-md-12">
        <div class="box">
          <!-- /.box-header -->
          <div class="box-body">
            
              <div class="row">
                <div class="col-md-6">
            <label for="yearFilter">Year:</label>
            <select class="form-control" v-model="selectedYear" @change="filterData">
              <option value="">All</option>
              <option v-for="year in uniqueYears" :key="year">{{ year }}</option>
            </select>
                </div>
                <div class="col-md-6">
            <label for="monthFilter">Month:</label>
            <select class="form-control" v-model="selectedMonth" @change="filterData">
              <option value="">All</option>
              <option v-for="(month, index) in months" :key="index" :value="index + 1">{{ month }}</option>
            </select>
                </div>
              </div>
        <div>
          <div class="row">
                <div class="col-sm-12">
          <table>
            <thead>
              <tr>
                <th rowspan="1" style="width: 200px;">LOCATION</th>
                <th rowspan="1">CONTRACT STAFF</th>
                <th rowspan="1">DEPARTURES</th>
                <th rowspan="1">NO. OF NEW CONTR HIRES (MTH)</th>
                <th rowspan="1">TOTAL STAFF FOR THE MONTH</th>
                <th rowspan="1">SJA ASSOCIATES - LOCUM</th>
                <th rowspan="1">LOCUM DEPARTURES</th>
                <th rowspan="1">SJA ASSOCIATES - VOLUNTEERS</th>
                <th rowspan="1">VOLUNTEER  DEPARTURES</th>
                <th style="width: 60px;" sortKey="month">MONTH</th>
                <th>YEAR</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(row, index) in filteredData" :key="index">
                <td>{{row.region}}</td>
                <td>{{row.contract_staff}}</td>
                <td>{{ row.depatures }}</td>
                <td>{{ row.new_contracts }}</td>
                <td>{{ row.total_staff_month }}</td>
                <td>{{ row.sja_associates }}</td>
                <td>{{ row.locum_depatures }}</td>
                <td>{{ row.sja_depatures }}</td>
                <td>{{ row.volunteer_depatures }}</td>
                <td>{{ months[row.month - 1] }}</td>
                <td>{{ row.year }}</td>
              </tr>
            </tbody>
            <tfoot>
              <tr>
                <td colspan="1">Totals</td>
                <td>{{ totalColumn('contract_staff') }}</td>
                <td>{{ totalColumn('depatures') }}</td>
                <td>{{ totalColumn('new_contracts') }}</td>
                <td>{{ totalColumn('total_staff_month') }}</td>
                <td>{{ totalColumn('sja_associates') }}</td>
                <td>{{ totalColumn('locum_depatures') }}</td>
                <td>{{ totalColumn('sja_depatures') }}</td>
                <td>{{ totalColumn('volunteer_depatures') }}</td>
              </tr>
            </tfoot>

          </table>
            <!-- <button class="btn btn-primary" > -->
              <vue-excel-xlsx
              class="btn btn-primary"
                :data="filteredData"
                :columns="columns"
                :file-name="'Exported Data'"
                :file-type="'xlsx'"
                :sheet-name="'Sheet 1'"
                >
                Download
            </vue-excel-xlsx>
          <!-- </button> -->
        </div>
        </div>
        </div>
        <!-- /.box-body -->
        </div>
        </div>
        </div>
  </div>
</template>
  </section>
</template>

<script>
import Axios from 'axios'
export default {
  data() {
    return {
      data: [],
      selectedYear: '',
      selectedMonth: '',
      months: [
        'January',
        'February',
        'March',
        'April',
        'May',
        'June',
        'July',
        'August',
        'September',
        'October',
        'November',
        'December'
      ],
      columns: [
        {
          label: 'LOCATION',
          field: 'region'
        },
        {
          label: 'CONTRACT STAFF',
          field: 'contract_staff'
        },
        {
          label: 'DEPARTURES',
          field: 'depatures'
        },
        {
          label: 'NO. OF NEW CONTR HIRES (MTH)',
          field: 'new_contracts'
        },
        {
          label: 'TOTAL STAFF FOR THE MONTH',
          field: 'total_staff_month'
        },
        {
          label: 'SJA ASSOCIATES - LOCUM',
          field: 'sja_associates'
        },
        {
          label: 'LOCUM DEPARTURES',
          field: 'locum_depatures'
        },
        {
          label: 'SJA ASSOCIATES - VOLUNTEERS',
          field: 'sja_depatures'
        },
        {
          label: 'VOLUNTEER  DEPARTURES',
          field: 'volunteer_depatures'
        },
        {
          label: 'MONTH',
          field: 'month'
        },
        {
          label: 'YEAR',
          field: 'year'
        }
      ]
    }
  },
  computed: {
    filteredData() {
      let filteredData = this.data

      if (this.selectedYear) {
        filteredData = filteredData.filter(row => row.year === parseInt(this.selectedYear))
      }

      if (this.selectedMonth) {
        filteredData = filteredData.filter(row => row.month === parseInt(this.selectedMonth))
      }

      return filteredData
    },
    uniqueYears() {
      return Array.from(new Set(this.data.map(row => row.year)))
    }
  },
  methods: {
    getData () {
      // You can add additional filtering logic here if needed
      Axios.get('http://178.79.130.84:5500/api/data/get/hr')
        .then((e) => {
          this.data = e.data.hr_data
        })
    },
    totalColumn(columnName) {
      return this.filteredData.reduce((total, row) => total + row[columnName], 0)
    },
    filterData() {
      let f = this.data

      if (this.selectedYear) {
        f = f.filter(row => row.year === parseInt(this.selectedYear))
      }

      if (this.selectedMonth) {
        f = f.filter(row => row.month === parseInt(this.selectedMonth))
      }

      this.filteredData = f
    },
    exportToExcel() {
      let exportdata = this.filteredData
      return exportdata.push({region: 'Total', contract_staff: this.totalColumn('contract_staff'), depatures: this.totalColumn('depatures'), new_contracts: this.totalColumn('new_contracts'), total_staff_month: this.totalColumn('total_staff_month'), sja_associates: this.totalColumn('sja_associates'), locum_depatures: this.totalColumn('locum_depatures'), sja_depatures: this.totalColumn('sja_depatures'), volunteer_depatures: this.totalColumn('volunteer_depatures')})
    }
  },
  mounted () {
    this.getData()
  }
}
</script>



